import './App.css';
import Header from './Components/Header'
import Home from './Components/Home'
import hero from './Assets/hero-image.jpg'
import LearnMore from './Components/LearnMore'
import Products from './Components/Products'
import About from './Components/About'
import { ScrollButton, useStyles } from './Components/Styles'
import FeatherIcon from 'feather-icons-react';
import { IconButton, useScrollTrigger, Zoom } from '@material-ui/core'
import { animateScroll } from 'react-scroll';

function ScrollTop(props) {
  const classes = useStyles();
  const { children, window } = props;

  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    disableHysteresis: true,
    threshold: 200,
  });

  const handleClick = (event) => {
    const anchor = (event.target.ownerDocument || document).querySelector('#home');

    if (anchor) {
      anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  };

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.zoom}>
        {children}
      </div>
    </Zoom>
  );
}



function App() {
  return (
    <main>
      {/* 
      
        Navbar 
        Switch -> Route -> Home || Products || Products.Solar || Products.Electrical || Products.ProcessCooling, 
        
        Home -> Scroll Links -> LearnMore, About Us

        LearnMore -> Routes Products -> Solar/Electrical/ProcessCooling

        AboutUs - Intro and Desc of Owners

        Products - Solar -> Solar component
                  Electrical -> Electrical Component
      
      */}

      <ScrollTop>
        <IconButton>
          <ScrollButton>
            <FeatherIcon icon="chevron-up" size={30} stroke='white' />
          </ScrollButton>
        </IconButton>
      </ScrollTop>

      <section id='header'>
        <Header />
      </section>


      <section id='home'>
        <div id='bg'>
          <img src={hero} alt="" />
        </div>

        <Home />

      </section>


      <section id='learn'>
        <LearnMore />
      </section>

      <section id='products'>
        <Products />
      </section>

      <section id='about'>
        <About />
      </section>



    </main>
  );
}

export default App;
