import React, { useState } from 'react'
import { Container, ButtonLink, useStyles } from './Styles'
import { Grid, Typography, Button } from '@material-ui/core'
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom'
import { scroller } from 'react-scroll'
import Electrical from '../Components/Electrical'
import Solar from '../Components/Solar'
import Home from '../Components/Home'
import ProcessCooling from '../Components/ProcessCooling'




export default function Products() {



    return (
        <Container>
            <Router>

                <Grid container alignItems='center' direction='row'>

                    <Grid item xs={12}>
                        <Typography variant='h2' align='center' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', marginTop: '5rem', fontWeight: '500' }}>
                            Products We Offer
                        </Typography>
                    </Grid>

                    <Grid item xs={12} alignItems='center' >
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', textAlign: 'center', margin: '2rem' }}>
                            <ButtonLink to='/solar'>
                                <Button variant="contaied" style={{
                                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                                    border: 0,
                                    borderRadius: 3,
                                    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                                    color: 'white',
                                    height: 48,
                                    padding: '0 30px',
                                    margin: '2rem',
                                }}>
                                    Solar
                                </Button>
                            </ButtonLink>



                            <ButtonLink to='/electrical'>
                                <Button variant="outlined" style={{
                                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                                    border: 0,
                                    borderRadius: 3,
                                    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                                    color: 'white',
                                    height: 48,
                                    padding: '0 30px',
                                    margin: '2rem',
                                }}>Electrical and Lightning</Button>
                            </ButtonLink>


                            <ButtonLink to='/process'>
                                <Button variant="outlined" style={{
                                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                                    border: 0,
                                    borderRadius: 3,
                                    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                                    color: 'white',
                                    height: 48,
                                    padding: '0 30px',
                                    margin: '2rem',
                                }}>Process Cooling</Button>
                            </ButtonLink>

                        </div>
                    </Grid>

                </Grid>

                <Switch>


                    <Route exact path='/solar' component={Solar} />
                    <Route exact path='/electrical' component={Electrical} />
                    <Route exact path='/process' component={ProcessCooling} />
                    <Redirect to='/' />
                </Switch>


            </Router>
        </Container>
    )
}
